import Button from "@material-ui/core/Button";
import { createStyles, Theme, WithStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import LaunchIcon from "@material-ui/icons/Launch";
import WebIcon from "@material-ui/icons/Web";
import React from "react";
import { Link } from "react-router-dom";

const ProfileLink: React.SFC = props => <Link to="/profile" {...props} />;
const SubmitDftLink: React.SFC = props => <Link to="/submit-dft" {...props} />;

export const styles = (theme: Theme) =>
  createStyles({
    button: {
      margin: theme.spacing.unit
    },
    rightIcon: {
      marginLeft: theme.spacing.unit
    }
  });

export interface IHomeProps extends WithStyles<typeof styles> {
  isValidAuth: boolean;
  checkAuth: () => void;
  login: () => void;
}

const HomeComponent: React.SFC<IHomeProps> = ({
  isValidAuth,
  checkAuth,
  login,
  classes
}) => (
  <div>
    <Typography variant="display1">Welcome to *untitled HORTON GUI*</Typography>
    {isValidAuth ? (
      <div>
        <Button
          variant="contained"
          className={classes.button}
          component={ProfileLink}
        >
          View Profile<WebIcon className={classes.rightIcon} />
        </Button>
        <Button
          variant="contained"
          className={classes.button}
          component={SubmitDftLink}
        >
          Submit DFT Job<AddCircleIcon className={classes.rightIcon} />
        </Button>
      </div>
    ) : (
      <Button variant="contained" className={classes.button} onClick={login}>
        Get Started<LaunchIcon className={classes.rightIcon} />
      </Button>
    )}
  </div>
);

export default HomeComponent;



